.constructor__content{
  width: 600px;
  max-height: 800px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 100px;
  padding-bottom: 40px;
}

.constructor__ingredients{
  width:568;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.constructor__ingredients * {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.constructor__info{
  width:568px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.wrapper__ingrediends{
  max-height: 440px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  overflow-y: scroll;
  overflow-x: hidden;
  margin-left: 16px;
  
}
.constructor__ingredients span:nth-child(1) {
  width:100%;
}
.constructor__ingredients span div {
  margin-right: 8px;
  margin-left: 8px;
  width:536px;
}
.wrapper__ingrediends span{
  cursor: pointer;
}

.wrapper__ingrediends::-webkit-scrollbar {
  width: 8px;
}

.wrapper__ingrediends::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px #8585AD; 
  -webkit-box-shadow: inset 0 0 6px #8585AD; 
  border-radius: 10px;
}

.wrapper__ingrediends::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: inset 0 0 100px #8585AD;
  -webkit-box-shadow: inset 0 0 100px #8585AD;
}

@media screen and (max-width: 1200px) {
  .constructor__content{
    padding-top: 16px;
  }
}
@media screen and (max-width: 767px) {
  .constructor__content{
    padding-top: 16px;
  }
}

